/**
 * 
 * @param {String} name 3-10
 */
export function userName(name) {
  var rule = /^[\u4e00-\u9fa5|a-zA-Z]{3,10}$/;
  return rule.test(name);
}
/**
 * 
 * @param {String} psd 6-18
 */

export function userPsd(psd) {
  var rule = /^[a-zA-Z\d]{6,18}$/;
  return rule.test(psd);
}
/**
 * 
 * @param {Number} tel 
 */

export function mobile(tel) {
  var rule = /^1[3|4|5|7|8]\d{9}$/;
  return rule.test(tel);
}
/**
 * 
 * @param {Number} code 
 */

export function code(num) {
  var rule = /^\d{4}$/;
  return rule.test(num);
}
/**
 * 
 * @param {String} name 2-8
 */

export function catname(name) {
  var rule = /^[\u4e00-\u9fa5|a-zA-Z|\d]{2,8}$/;
  return rule.test(name);
}
/**
 * 
 * @param {String} str 20
 */

export function title(str) {
  return str && str.length <= 20;
}
/**
 * 
 */

export function noEmpty(str) {
  return !!str;
}